import * as React from "react";

import { SITE_URL, TWITTER } from "../../../nubarchiva/settings/metadata";
import { defaultLanguage } from "../../../nubarchiva/settings/translations";
import { cleanSlashInPath } from "../shared/utils/cleanSplash";
import { getAbsoluteUrl } from "../shared/utils/getAbsoluteUrl";
import { getCodeLang } from "../shared/utils/getCodeLang";
import { IPropsSEO } from "./iseo";

const titleDefault = "Nubarchiva";
const descriptionDefault = "Software libre para la preservacin de la memoria colectiva y el acervo cultural";

export const SEO: (props: IPropsSEO) => JSX.Element = (props: IPropsSEO) => {
  const siteUrlWithLang = props.lang === defaultLanguage ? SITE_URL : `${SITE_URL}${props.lang}/`;
  const siteUrlThumbnail = `${SITE_URL}assets/thumbnails/`;
  const title = props?.title || titleDefault;
  const description = props?.description || descriptionDefault;
  const robots = props?.robots || "index, follow";

  const contentAbsoluteUrl = getAbsoluteUrl(
    props?.canonical === "/" ? siteUrlWithLang : props?.canonical || "",
    siteUrlWithLang
  );

  const getUrlOriginal = () => {
    const hrefLang = props?.hrefLangList?.find((item) => item.hrefLang === defaultLanguage);
    if (hrefLang) {
      return cleanSlashInPath(`${SITE_URL}${hrefLang.slug}/`);
    }
    return contentAbsoluteUrl;
  };
  const canonical = props.isNotOriginalContent ? getUrlOriginal() : contentAbsoluteUrl;

  const thumbnail = getAbsoluteUrl(props.thumbnailWithExtension || "home.png", siteUrlThumbnail);

  const hrefLanglist_render: JSX.Element[] = [];

  props?.hrefLangList?.forEach((item, index) => {
    const compositionUrl = `${SITE_URL}${
      item.hrefLang === defaultLanguage ? `${item.slug}/` : `${item.hrefLang}/${item.slug}/`
    }`;
    const url = compositionUrl.replace(/[\\/]{2,}/g, "/").replace("https:/", "https://");
    hrefLanglist_render.push(
      <link key={index} rel="alternate" href={cleanSlashInPath(`${url}`)} hrefLang={getCodeLang(item.hrefLang)} />
    );
    if (item.hrefLang === defaultLanguage) {
      hrefLanglist_render.push(
        <link key={index} rel="alternate" href={cleanSlashInPath(`${url}`)} hrefLang="x-default" />
      );
    }
  });
  return (
    <>
      {props.lang && <html lang={getCodeLang(props.lang)} />}
      <meta name="description" content={description} />
      <meta name="image" content={thumbnail} />

      <meta property="og:url" content={`${contentAbsoluteUrl}`} />
      <meta property="og:type" content="website" />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={description} />
      <meta property="og:image" content={thumbnail} />

      {hrefLanglist_render}

      <meta name="twitter:card" content="summary" />
      <meta name="twitter:creator" content={TWITTER} />
      <meta name="twitter:site" content={TWITTER} />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      <meta name="twitter:image:src" content={thumbnail} />
      <link rel="canonical" href={`${canonical}`} />
      <title>{title}</title>
      <meta name="robots" content={robots} />
    </>
  );
};
